<template>
<div id="carouselExampleRide" class="carousel slide"  data-bs-ride="carousel">
  <div class="carousel-indicators">
    <button type="button" data-bs-target="#carouselExampleRide" v-for="(item,index) in CoverList" :key="item.id"
    :data-bs-slide-to="index" :class="{active:(index==0)}" aria-current="true" :aria-label="'Slide '+(index+1)"></button>
  </div>
  <div class="carousel-inner h-100 overflow-hidden" >
    <div class="carousel-item active" v-for="(item) in CoverList" :key="item.id">
      <img :src="item.bgImg" class="d-block img-fluid rounded object-fit-contain w-100 h-100 " alt="...">
    </div>
  </div>
  <button class="carousel-control-prev" type="button" data-bs-target="#carouselExampleRide" data-bs-slide="prev">
    <span class="carousel-control-prev-icon" aria-hidden="true"></span>
    <span class="visually-hidden">Previous</span>
  </button>
  <button class="carousel-control-next" type="button" data-bs-target="#carouselExampleRide" data-bs-slide="next">
    <span class="carousel-control-next-icon" aria-hidden="true"></span>
    <span class="visually-hidden">Next</span>
  </button>
</div>
</template>

<script>
import {SwiperImg} from "@/api/List"
export default {
  name: "CoverSwiper",

  data() {
    return {
        CoverList:[]
    };
  },
  components: {},
  mounted() {
    SwiperImg().then((res)=>{
        if(res.status==200){
            this.CoverList=res.ImgList
        }
    }).catch((err)=>{
        console.log(err)
    })
  }
};
</script>

<style lang="scss" scoped>
.carousel{
    height: 400px;
}
</style>
